.detail{
    width:20%;
    min-width:100%;
    height: 100%;
    padding: 2rem;
    position: fixed;
    right: 0;
    background-color: white;
    overflow: auto;
    box-shadow: rgba(100,100,111,0.2)0px 7px 29px 0px;
    
}

.detail-header{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.detail-header img{
    width:50px;
    height: 50px;
}
.divider{
    background-color: grey;
    margin-top:10px;
    margin-bottom:20px
}

.detail-desc p{
    line-height: 1.5;
    color: grey;
    z-index: -1;
    
}
h4{
    color: black;
    margin-bottom:10px
}
a{
    color: var(--primaryColor);
}

.btn{
   
    background-color: var(--primaryColor);
    color:white;
    border: none;
    border-radius:20px;
    width:100px;
    height: 30px;
    padding: 5rem,0;
    margin-top: 15px;
    z-index: inherit;

    box-shadow: rgba(0,0,0,0.35), 10px,25px,5px,10px ;
}

.btn:hover {
    background-color:white;
    color: var(--primaryColor) ;
    box-shadow: rgba(0,0,0,2.4)0px,3px,8px;
}
