@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  text-align: center;
  --primaryColor:#4c50d3;
  --secondaryColor:yellow;
  --fontColor:black;
  --display:block;


}
*{
  margin: 0%;
  padding:0%;
  box-sizing:border-box;
  outline: none;
  font-family: 'Lato',sans-serif;

}

body {
  width: 100%;
  height: 100%;
  
}




.main{
    width:calc(100%-20%-25%);
    min-height:100%;
    min-width: 300px;
    height: 1000px;
    display: flex;
    flex-direction: column;
    padding: 2rem 2%;
   
    background-color:snow ;
   
    margin-left: 20%;
    margin-right:25%;
    z-index: inherit;
   
}
.main-header{
    display: flex;
    align-items: center;
    grid-gap:1rem;
    gap:1rem;

    
}
.menu-bar {
    
    color: black;
    padding: 0.5rem;
    display: none;
    
}
.search {
    width: 100%;
    display: flex;
    justify-content: space-between;
    border: none;
    background-color: white;
    border-radius: 10px;
    position:relative;
    padding: 0.5rem 1rem;
    grid-gap:1rem;
    gap:1rem;

   
}
.search input {
    width: 90%;
    border: none;
    
  
}
.btn-search{
    position: absolute;
    right: 0;
    top: 0;
   height: 100%;
    padding: 0 5%;
    border: none;
    background-color: var(--primaryColor);
    color: white;
    font-size: 18px;
    border-radius: 20px;
    box-shadow: rgba(0,0,0,0.16) 0px 1px 4px;
    cursor: pointer;
}
.btn-search:hover{
    background-color: var(--secondaryColor);
    color: black;
}  


       


@media  screen and (max-width:880px) {
    .menu-bar{
      display: block;
    }
    .main{
        width: calc(100%-40%);
        margin-left: 0;
        margin-right:30%;
     
      }

}
@media  screen and (max-width:780px) {
 
    
    .main{
     width:70%
    }


}
@media  screen and (max-width:700px) {
 
    
    .main{
     width:100%
    }
   


}

.filter-wrapper{
    margin: 1rem 0;
    font-size: 14px;
}

.filter{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    overflow-x: auto;
    margin: 0px 5rem;
}
.filter-wrapper p {
    display: flex;
    justify-content: flex-start;
}

.btn-filter{
    min-width: 110px;
    padding: 0px,5rem;
    border-radius: 20px;
    border: none;
    background-color: white;
    cursor: pointer;
   margin: 1px;
}

.btn-filter:hover {
    background-color:var(--primaryColor);
    color: white;
    box-shadow: rgba(0,0,0,2.4)0px,3px,8px;
}
.sort {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    grid-gap: 0.5rem;
    gap: 0.5rem;
} 
.sort p{
font-size: 14px ;
}
.sort-list select{
    height: 1.5rem;
    border: none;
    border-radius: 20px;
    margin: 0,2px;
}

.wrapper{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem 0;
    grid-gap: 1rem;
    gap: 1rem;
    
}
.card{
    display: flex;
    flex-wrap:wrap;
    flex-direction: row;
    padding: 3%;
    margin: 0 1%;
    background-color: white;
    line-height: 1.5;
    grid-gap : 1.5rem;
    gap : 1.5rem;
    box-shadow: rgba(0,0,0,0.16) 0px 1px 4px;
    border-radius: 10px;
    cursor: pointer;
}
.card:hover{
    box-shadow: rgba(0,0,0,0.24) 0px 3px 8px;
}
.card-left{
    width: 10%;
    min-width: 50px;
    display: flex;
    justify-content: center;
    border: 20%;
    
}
.card-left img{
    width:100%;
    height: auto;
    min-height: 60px;
    padding: 1rem;
    border-radius: 20%;
}
.card-center {
    width: 50%;

}
.card-center p,h3 {
 display: flex;
   align-items: flex-start;
}
.card-sub {
    display: flex;

    flex-wrap: wrap;
}
.card-sub, .card-loc{
    font-size: 13px;
    color: darkgray;

}

.card-sub p {
    display: flex;
    padding: 0 0.5rem 0 0;
    align-items: center;
}

.card-right {
    width: 15%;
    
}
a{
    text-decoration: none;
    color : grey;
    font-size: 13px;
}
.card-salary{
    color: var(--primaryColor);
    padding: 2.5rem,0;
    margin-top: 15px;
}
span{
    color: black;
}
img:hover{
    background-color: lightblue;
}
@media  screen and (max-width:880px) {
 
    
    .card-left img{
     width:120%;
     height: 80%;
    }


}
@media  screen and (max-width:700px) {
 
    
   .card-left img{
     height:80%;
     width:150%
    }


}

.detail{
    width:20%;
    min-width:100%;
    height: 100%;
    padding: 2rem;
    position: fixed;
    right: 0;
    background-color: white;
    overflow: auto;
    box-shadow: rgba(100,100,111,0.2)0px 7px 29px 0px;
    
}

.detail-header{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.detail-header img{
    width:50px;
    height: 50px;
}
.divider{
    background-color: grey;
    margin-top:10px;
    margin-bottom:20px
}

.detail-desc p{
    line-height: 1.5;
    color: grey;
    z-index: -1;
    
}
h4{
    color: black;
    margin-bottom:10px
}
a{
    color: var(--primaryColor);
}

.btn{
   
    background-color: var(--primaryColor);
    color:white;
    border: none;
    border-radius:20px;
    width:100px;
    height: 30px;
    padding: 5rem,0;
    margin-top: 15px;
    z-index: inherit;

    box-shadow: rgba(0,0,0,0.35), 10px,25px,5px,10px ;
}

.btn:hover {
    background-color:white;
    color: var(--primaryColor) ;
    box-shadow: rgba(0,0,0,2.4)0px,3px,8px;
}

.sidebar{
    width:20%;
    max-width: 20%;
    height:100%;
    flex-direction:column;
    align-items: flex-start;
    justify-content: space-between;
    padding: 2rem;
    background-color: var(--primaryColor);
    color: white;
    position: fixed;
    left: 0;
}

.logo{
    letter-spacing: 2;
    margin-bottom: 80px;
   

}
.menu{
    display: flex;
    flex-direction: column;
    grid-gap: 2rem;
    gap: 2rem;
}
.menu .a {
    display: flex;
    align-items: center;
    color: white;
   
    text-decoration: none;
    font-size: 15px;
}
.menu svg {
    margin-right: 0.5rem;
}
.menu a:hover {
    color:var(--secondaryColor);
}

#open {
display: block;
}



@media  screen and (max-width:880px) {
 
    .sidebar{
        flex-direction: row;
        height:20%;
        top:0;
       width: 100%;
       max-width:70%;
       position: relative;

    }
    .menu {
        flex-direction: row;
        align-items: flex-start;

    }
    .menu a {
        flex-direction: row;
        justify-content: space-evenly;
        height: 20px;
        top:0;
        width: 100%;
        
        
       
        
    }
    .menu #a-l{
        display: none;
    }
   
 .logo{
    display: none;
 }
 .profile {
    display: none;
 }
 
 .profile-name {
    display: none;
 }
 .detail {
    top: 0;
 }
}
@media  screen and (max-width:700px) {
 
    
    .sidebar{
      max-width: 100%;
    }


}
.profile{
    display: flex;
    flex-direction: row;
    align-items: center;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    padding: 1rem;
    background-color: white;
    color:var(--fontColor);
    border: none;
    border-radius: 10px;
    margin-bottom: 50px;
 
}

.profile:hover{
    background-color:var(--secondaryColor);
}

.profile img {
    width :30px;
    height: 30px;
    border-radius:50%;

}
.profile p {
    font-size: 12px;
}

@media screen and (max-width:1000px){
    .profile p {
        display: none;
    }
  
    
}
@media screen and (max-width:880px){
    .profile  {
        display: none;

    }
    
}
